import React, { Component } from 'react';
import Nav from './Nav';
import Footer from './Footer';
import Modal from './Modal';

export default class Layout extends Component {
  render() {
    return (
      <div className="main-container">
        <Modal />
        <Nav />
        <div className="container">
        {this.props.children}
        </div>
        <Footer />
      </div>
    )
  }
}


