export default function reducer(state = {
  html: '',
  fetching: false,
  fetched: false,
  error: null,
}, action) {
  switch (action.type) {
    case 'FETCH_HTML_PENDING': {
      return {
        ...state,
        fetching: true,
        fetched: false,
      }
    }
    case 'FETCH_HTML_FULFILLED': {
      return {
        ...state,
        html: action.payload,
        fetching: false,
        fetched: true,
      }
    }
    case 'FETCH_HTML_REJECTED': {
      return {
        ...state,
        fetching: false,
        fetched: false,
        error: action.payload,
      }
    }
    case 'ACK_ERROR': {
      return {
        ...state,
        error: null,
      }
    }
    default: { }
  }
  return state;
}


