export default function reducer(state={
  userID: null,
  username: 'Unknown',
  password: '',
  loggingIn: false,
  loggedIn: false,
  entitlements: [],
  enabled: false,
  error: null,
}, action) {
  switch (action.type) {
    case 'LOGIN_PENDING': {
      return {...state,loggingIn: true};
    }
    case 'LOGIN_REJECTED': {
      return {...state, loggingIn: false, loggedIn: false, error: action.payload};
    }
    case 'LOGIN_FULFILLED': {
      return {
        ...state, 
        loggingIn: false, 
        loggedIn: true, 
        userID: action.payload.userID,
        username: action.payload.username, 
        entitlements: action.payload.entitlements, 
        enabled: action.payload.enabled
      };
    }
    default: {
      return {state};
    }
  }
}


