export default function reducer(state = {
  open: false,
  image: null,
  main_message: '',
  sub_message: '',
}, action) {
  switch (action.type) {
    case 'OPEN_MODAL': {
      return {
        ...state,
        open: true,
        image: action.payload.image,
        main_message: action.payload.main_message,
        sub_message: action.payload.sub_message,
      }
    }
    case 'CLOSE_MODAL': {
      return {
        ...state,
        open: false,
        image: null,
        main_message: '',
        sub_message: '',
      }
    }
    default: { }
  }
  return state;
}


