export function openModal(img, msg) {
  return dispatch => {
    dispatch({
      type: 'OPEN_MODAL',
      payload: { image: img, main_message: msg.main, sub_message: msg.sub.message },
    })
  }
}

export function closeModal() {
  return dispatch => {
    dispatch({
      type: 'CLOSE_MODAL',
    })
  }
}


