import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './index.css';

export default class Footer extends Component {
  render() {
    return (
      <footer className="page-footer blue center-on-small-only">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-4">
              <h5>Betting Sites</h5>
              <ul>
                <li><a href="http://topbet.eu/">TopBet</a></li>
                <li><a href="https://www.betdsi.eu/">BetDSI</a></li>
                <li><a href="https://www.bovada.lv/">Bovada</a></li>
              </ul>
            </div>
            <div className="col-md-4">
              <h5>Odds</h5>
              <ul>
                <li><a href="http://www.oddsshark.com/">OddsShark</a></li>
              </ul>
            </div>
            <div className="col-md-4">
              <h5 className="title">Site Map</h5>
              <ul>
                <li>{<Link to="/">Home</Link>}</li>
              </ul>
            </div>
          </div>
        </div>
        <div className="footer-copyright">
          <div className="container-fluid">
            <a href="https://www.google.com"> Google </a>
          </div>
        </div>
      </footer>
    );
  }
}


