import React, { Component } from 'react';
import { connect } from 'react-redux';
import Modal from 'react-modal';
import './index.css';

import * as ModalActions from '../../actions/modalActions';

class ModalHandler extends Component {
  componentWillMount() {
    Modal.setAppElement('body');
  }

  render() {
    return (
      <Modal isOpen={this.props.modal.open} style={modalStyle}>
        <div className="modal-container">
          <div className="modal-header">
            <img id="modal-img" src={this.props.modal.image} alt='Modal' />
            <h3 className="modal-main-msg">{this.props.modal.main_message}</h3>
          </div>
          <div className="modal-sub-container">
            <small className="modal-sub-msg">{this.props.modal.sub_message}</small>
          </div><hr />
          <button className="btn btn-danger modal-ok" onClick={() => this.props.dispatch(ModalActions.closeModal())}>OK</button>
        </div>
      </Modal>
    )
  }
}

const modalStyle = {
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.75)'
  },
  content: {
    top: '33%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    border: '1px solid #ccc',
    background: '#fff',
    overflow: 'auto',
    WebkitOverflowScrolling: 'touch',
    borderRadius: '4px',
    outline: 'none',
    padding: '20px',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  }
}

export default connect((store) => { return { modal: store.modal, } })(ModalHandler);


