import axios from 'axios';

export function getHTML() {
  return dispatch => {
    dispatch({
      type: 'FETCH_HTML',
      payload: axios.get('http://www.rosstucker.com/even-money-picks/'),
    })
  }
}

export function ackError() {
  return dispatch => {
    dispatch({
      type: 'ACK_ERROR',
    })
  }
}


