import React, { Component } from 'react';
import { connect } from 'react-redux';
import './index.css';
import { Error } from '../../images';

import * as EvenMoneyActions from '../../actions/evenMoneyActions';
import * as ModalActions from '../../actions/modalActions';

class EvenMoney extends Component {
  componentDidMount() {
    //this.props.dispatch(EvenMoneyActions.getHTML());
  }

  handleError() {
    this.props.dispatch(ModalActions.openModal(Error, {main: 'Something went wrong...', sub: this.props.evenmoney.error}));
    this.props.dispatch(EvenMoneyActions.ackError());
  }

  render() {
    return (
      <div>
        {/*
          this.props.evenmoney.error
          ? this.handleError()
          : null
        */}
        <iframe src="http://www.rosstucker.com/even-money-picks/" title="Even Money"></iframe>
      </div>
    )
  }
}

export default connect((store) => {
  return {
    evenmoney: store.evenmoney,
    modal: store.modal,
  }
})(EvenMoney)


